@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600&family=Ubuntu:wght@300;400;500;700&display=swap);
*{
  font-family: 'Mulish', sans-serif;
} 

body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  width: 30%;
  height: 800px;
  background-color: black;
  padding: 30px 15px;
  border-radius: 30px;
  margin: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
}

.search-input{
    border: 3px solid whitesmoke;
    border-radius: 30px;
    background-color: black;
    width: 70%;
    height: 10px;
    padding: 15px;
    margin-bottom: 5%;
  }

.search-input::-webkit-input-placeholder{
    color: rgb(239, 254, 255);
  }

.search-input::placeholder{
    color: rgb(239, 254, 255);
  }

input, select, textarea{
    color: rgb(239, 254, 255);
}

.search-input:focus{
    outline: none !important;
    border-color: whitesmoke;
}

.info-about-contact{
    width: 70%;
    height: 60px;
    padding: 15px;
    color: rgb(239, 254, 255);
    border: 3px solid whitesmoke;
    border-radius: 30px;
    margin:2% 0 2% 0;
    display: flex;
    flex-direction: column;
}

.phone{
    margin-top: 3%;
}

.name-and-gender{
    font-size: 20px;
    width: 80%;
    display: flex;
    justify-content: space-between;
}


